<template>
  <div id="contenedorMain">
    <div class="container">
      <div id="containerMoneda" class="glass">
        <div id="moneda">
          <p>{{ opcion1 }}</p>
        </div>
        <button id="rotar" @click="rotar()">Girar</button>
      </div>
      <div id="inputs" class="glass">
        <h2>Ingrese 2 opciones</h2>
        <input type="text" v-model="opcion1" />
        <input type="text" v-model="opcion2" />
      </div>
    </div>
    <button id="volver">
      <router-link to="/">Volver al inicio</router-link>
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      opcion1: "Cara",
      opcion2: "Cruz",
    };
  },
  methods: {
    rotar() {
      //asigna a variables los valores de las opciones
      let varA = this.opcion1;
      let varB = this.opcion2;
      let num = 0;
      //obtiene un num random entra 2 y 10
      let vueltas = Math.random() * 15 + 5;
      vueltas = Math.round(vueltas);
      let moneda = document.getElementById("moneda");
      //el intervalo gira hasta que num alcanza 360*vueltas
      //y cambia el texto segun el lado de la moneda
      var intervalo = setInterval(function () {
        moneda.style.transform = "rotateY(" + num + "deg)";
        num += 10;
        if (num % 360 < 90 || num % 360 > 270) {
          moneda.innerHTML = varA;
        }

        if (num % 360 > 90 && num % 360 < 270) {
          moneda.innerHTML = '<p class="texto">' + varB + "</p>";
          moneda.querySelector(".texto").style.transform = "rotateY(180deg)";
        }
        if (num >= 180 * vueltas) {
          clearInterval(intervalo);
        }
      }, 10);
    },
  },
};
</script>

<style scoped>
.container {
  display: flex;
  flex-direction: row;
  gap: 30px;
  align-content: center;
  justify-content: center;
  width: 80vw;
}

#inputs {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-content: center;
  justify-content: center;
}

h2 {
  text-align: center;
}
#containerMoneda{
    width: 50%;
    height: 60vh;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    justify-content: space-around;
}
#moneda {

  background: #fdc830; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #f37335,
    #fdc830
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left,
    #f37335,
    #fdc830
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  box-shadow: 0px 30px 70px 3px rgba(0, 0, 0, 0.75);
  width: 200px;
  height: 200px;
  
  border-radius: 50%;
  text-align: center;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transform: rotate(0, 0);
}
#rotar{
  background-color: rgb(1, 194, 49);
  width: 400px;
}
#rotar:hover{
  background-color: rgb(0, 255, 64);
  cursor: pointer;
}
p {
  max-width: 100px;
  max-height: 100px;
}
</style>