<template>
  <div id="contenedorMain">
    <div id="containerOpciones" >
      <div id="agregar" class="glass">
        <h3>Insertar opciones:</h3>
        <div id="containerInput">
          
        <input placeholder="Inserte una opcion" type="text" v-model="nuevaOpcion" @keyup.enter="agregar" />
        <button @click="agregar">Agregar</button>
        </div>

        <div id="listadoOpciones" >
          <p v-for="opcion in opciones" v-bind:key="{ backgroundColor: opcion.colorFondo }">
            {{ opcion.titulo }}
            <button @click="eliminar(opcion.titulo)">X</button>
          </p>
        </div>
      </div>
     <div id="containerRuleta" class="glass">
        <div id="flecha"></div>
        <div id="ruleta"> </div>
        <div id="botonera">
    <button @click="girar" id="girar">Girar</button>
    <button @click="eliminarTodo" id="limpiar">Limpiar lista</button>
    </div>
      </div>
    </div>
    <button id="volver"><router-link to="/" >Volver al inicio</router-link></button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      nuevaOpcion: "",
      opciones: []
    };
  },
  methods: {
   
    agregar() {
      if (this.nuevaOpcion) {
        this.opciones.push({
          titulo: this.nuevaOpcion,
          colorFondo: this.generarColor()
        });
        this.nuevaOpcion = "";
        this.actualizarRuleta()
      }
    },
    eliminar(title) {
      let i = 0;
      let largo = this.opciones.length;
      let seguir = true;
      while (i < largo && seguir) {
        console.log(this.opciones[i].titulo);
        if (this.opciones[i].titulo == title) {
          seguir = false;
        } else {
          i++;
        }
      }
      this.opciones.splice(i, 1);
      this.actualizarRuleta();
    },
    eliminarTodo() {
      this.opciones = [];
      this.actualizarRuleta()
    },
    generarColor() {
      //genera con color aleatorio en rgb
      let varR = Math.floor(Math.random() * 255);
      let varG = Math.floor(Math.random() * 255);
      let varB = Math.floor(Math.random() * 255);
      let color = "rgb(" + varR + ", " + varG + ", " + varB + ")";
      return color;
    },
    actualizarRuleta(){
        let ruleta = document.getElementById("ruleta");
        let opcTotal = this.opciones.length;
        let porcentaje = 360/opcTotal;
        let valorInicio = 0;
        let valorFin = porcentaje;
        let acumColor = "conic-gradient( "
        let i = 0;
        if(opcTotal == 0){
            document.getElementById("ruleta").style.background = "transparent";
        }else {
            for(i = 0; i<opcTotal; i++){
            acumColor = acumColor + " "+ this.opciones[i].colorFondo +" "+ valorInicio +"deg "+ valorFin + "deg";
            if(i<opcTotal-1){
                acumColor = acumColor + ","
            }
            valorInicio = valorInicio + porcentaje;
            valorFin = valorFin + porcentaje 
        }
        acumColor = acumColor + ")";
        ruleta.style.background = acumColor;
        }
        
    },
    girar(){
        let num = 0;
        let vueltas = Math.random()*100 +15;
        vueltas = Math.round(vueltas);
        console.log(vueltas)
        var intervalo = setInterval(function(){ 
            document.getElementById("ruleta").style.transform = "rotateZ("+num+"deg)";
            num+=10;
            vueltas = vueltas -1;
            if(vueltas == 0){
                clearInterval(intervalo);
            }
            }, 40);
    }
    }
};
</script>

<style scoped>

#agregar {
    overflow: scroll;
    overflow-x: hidden;
    
}
h3{
  text-align: center;
  padding: 10px 0
}

#containerOpciones {
  display: flex;
  gap: 40px;
   max-height: 600px;
}
#containerRuleta{
  display: flex;
  flex-direction: column;
  width: 50%;
  align-items: center;
}
#botonera{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0px
}
#ruleta {
  width: 450px;
  height: 450px;
  border: 2px black solid;
  border-radius: 50%;
  padding: 20px
}

#flecha {
    width: 0;
    height: 0;
    border-top: 40px solid black;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    position: relative;
    top: 40px;
    z-index: 100;
}
#girar{
  background-color: rgb(1, 194, 49);
  width: 400px;
}
#girar:hover{
  background-color: rgb(0, 255, 64);
}
#limpiar{
  background-color: #fd4343;
  width: 300px
}
#limpiar:hover{
   background-color: #fc5555;
}
#containerInput{
  display: flex;
  gap: 10px;
  padding: 0 5px 10px 5px;

}
input{
    border-style: none;
    padding: 10px;
    border-radius: 3px;
    width: 500px;
}
p{
  display: flex;
  justify-content: space-between;
  padding: 10px
}
</style>
